.headerMenu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menuIcon {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


@media screen and (max-width:821px) {
    .headerMenu {
        display: none;
    }
}

@media screen and (min-width:821px) {
    .menuIcon {
        display: none;
    }
}