.fixed-button {
    position: fixed;
    right: 2em;
    top: 30%;
}
.vip-card {
    background: linear-gradient(135deg, #fffaf7, #fff9f7 0.01%, #fff4f4);
    box-shadow: inset 0 0 0 2px #ff8181;
    padding: 1em;
    border-radius: 10px;
    position: relative;
    font-family: '微软雅黑';
    text-align: center;
    padding-bottom: 2em;
}
.vip-card h3 {
    font-weight: bold;
}
.vip-card p {
    font-family: '新宋体';
    line-height: 1;
}
.vip-card span {
    font-size: 2em;
}
.vip-card:hover {
    opacity: 0.7;
}
.vip-bottom {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    left: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #ff8181;
    opacity: 0.5;
    font-size: 12px;
    padding: 0.25em 0;
}
@media screen and (max-width: 1200px) {
    .fixed-button {
        position: static;
        right: 2em;
        top: 30%;
   }
    .fixed-button button {
        width: 100%;
   }
    .w-md-100 {
        width: 100%;
   }
}
