.adviser .avatar {
    width: 10vw;
    height: 10vw;
    margin: 1em auto;
    overflow: hidden;
    border-radius: 50%;
}
.adviser .avatar img {
    width: 100%;
}
.adviser-detail .avatar {
    width: 16vw;
    height: 16vw;
    overflow: hidden;
    border-radius: 50%;
    margin: 1em auto;
}
.adviser-detail .avatar img {
    width: 100%;
}
.adviser p {
    margin: 0;
    margin-bottom: 0.5em;
}
@media screen and (max-width: 768px) {
    .adviser .avatar {
        width: 60vw;
        height: 60vw;
        /* color:royalblue */
   }
}
